<template>
  <div class="topRight list_box">
    <div id="topRight"></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      option: {},
    };
  },
  methods: {
    createData() {
      var chartDom = document.getElementById("topRight");
      var myChart = this.$echarts.init(chartDom);
      var option;

      option = {
        title: {
          text: "外租单位用能明细(kWh)",
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          bottom: "15",
          textStyle:{
            color:'#fff'
          }
        },
        series: [
          {
            type: "pie",
            radius: "50%",
            data: [
              { value: 1048, name: "东智" },
              { value: 735, name: "日生" },
              { value: 580, name: "晶立诚" },
              { value: 484, name: "富康城" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            label:{
              color:'#fff',
              formatter: '{b}: {c}({d})%'
            }
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData();
  },
};
</script>

<style lang="less" scoped>
#topRight {
  width: 100%;
  height: 100%;
}
.topRight {
  width: 33%;
  padding-top: 20px;
  margin-left: 10px;
  box-sizing: border-box;
}
</style>
